import { IClientSideComponentManifest } from '@microsoft/sp-module-interfaces';
import { IApplicationPreloadedData, ensureDebugAllowed } from './SPModuleLoader';

/**
 * This is a subset of the V1 manifest, included here for the sake of typesafety when extracting a V2 manifest
 * from the "properties" property of the V1 manifest.
 */
export interface IPartialV1Manifest {
  id: string;
  properties: { manifest: IClientSideComponentManifest | string };
}

export interface IManifestStoreEntry {
  /**
   * The manifest's ID.
   */
  id: string;

  /**
   * The manifest V2 payload.
   */
  manifest: IClientSideComponentManifest;

  /**
   * A debug manifest V2 payload, if one has been provided.
   */
  debugManifest?: IClientSideComponentManifest;
}

/**
 * This class maintains a cache of the manifests on the current page.
 */
export default class ManifestStore {
  private static _instance: ManifestStore;

  public static get instance(): ManifestStore {
    if (!this._instance) {
      this._instance = new ManifestStore();
    }

    return this._instance;
  }

  private _manifests: Map<string, IManifestStoreEntry> = new Map<string, IManifestStoreEntry>();

  /**
   * Populate the manifest store with the preloaded manifests.
   *
   * @param {preloadedData} The preloaded data
   */
  public populateManifests(preloadedData: IApplicationPreloadedData): void {
    if (preloadedData) {
      if (preloadedData.clientSideApplicationManifest) {
        this._addManifest(preloadedData.clientSideApplicationManifest, true);
      }

      if (preloadedData.manifests && preloadedData.manifests.length) {
        this.importManifests(preloadedData.manifests, true);
      }
    }

    /* tslint:disable:no-any */
    const globalManifests: IPartialV1Manifest[] = (window as any).g_webPartManifests;
    /* tslint:enable:no-any */
    if (globalManifests && globalManifests.length) {
      this.importManifests(globalManifests, true);
    }
  }

  /**
   * Populate the manifest store with debug manifests.
   *
   * @param {manifests} A dictionary of debug manifests.
   */
  public populateDebugManifests(manifests: { [id: string]: IClientSideComponentManifest }): void {
    if (manifests) {
      /* tslint:disable:forin */
      for (const id in manifests) {
      /* tslint:enable:forin */
        if (!this._manifests.get(id)) {
          this._manifests.set(id, {
            id: id,
            manifest: undefined
          });
        }

        this._manifests.get(id).debugManifest = manifests[id];
      }
    }
  }

  /**
   * Return a manifest by its ID. Only return a debug manifest is debug is allowed. If a manifest isn't found by
   * the provided ID, return undefined.
   *
   * @param {id} The ID of the manifest to retrieve.
   * @param {allowDebug} If true, return a debug manifest if one exists.
   * @returns The retrieved manifest, or undefined if one could not be found.
   */
  public getManifestById(id: string): IClientSideComponentManifest {
    const manifestEntry: IManifestStoreEntry = this._manifests.get(id);
    return this._getManifestFromStoreEntry(manifestEntry);
  }

  /**
   * Get all registered manifest objects.
   *
   * @returns The registered manifest objects.
   */
  public getAllManifests(): IClientSideComponentManifest[] {
    const result: IClientSideComponentManifest[] = [];

    this._manifests.forEach((manifestEntry: IManifestStoreEntry) => {
      const manifest: IClientSideComponentManifest = this._getManifestFromStoreEntry(manifestEntry);
      if (manifest) {
        result.push(manifest);
      }
    });

    return result;
  }

  /**
   * Returns a map of manifest IDs to manfiests.
   *
   * @return {{ [key: string]: IManifestStoreEntry }}: A map of manifest IDs to manfiests.
   */
  public getManifestMap(): { [key: string]: IManifestStoreEntry } {
    const result: { [key: string]: IManifestStoreEntry } = {};
    this._manifests.forEach((value: IManifestStoreEntry, key: string) => result[key] = value);

    return result;
  }

  /**
   * Loads additional manifests into the manifest store, updating existing manifests.
   *
   * @param {manifests} The manifests to load into the store.
   */
  public importManifests(manifests: (IPartialV1Manifest | IClientSideComponentManifest)[],
                         overwriteExisting: boolean): void {
    manifests.forEach((manifest: IClientSideComponentManifest) => this._addManifest(manifest, overwriteExisting));
  }

  private _getManifestFromStoreEntry(manifestEntry: IManifestStoreEntry): IClientSideComponentManifest {
    const allowDebug: boolean = ensureDebugAllowed(false);

    if (manifestEntry) {
      if (allowDebug && manifestEntry.debugManifest) {
        return manifestEntry.debugManifest;
      } else {
        return manifestEntry.manifest;
      }
    } else {
      return undefined;
    }
  }

  private _addManifest(manifest: IPartialV1Manifest | IClientSideComponentManifest, overwriteExisting: boolean): void {
    const v1Manifest: IPartialV1Manifest = manifest as IPartialV1Manifest;
    const v2Manifest: IClientSideComponentManifest = manifest as IClientSideComponentManifest;

    const manifestEntry: IManifestStoreEntry = { id: manifest.id, manifest: v2Manifest };
    if (v1Manifest.properties && v1Manifest.properties.manifest) {
      // Looks like we're in a v1.5 manifest
      if (typeof v1Manifest.properties.manifest === 'string') {
        manifestEntry.manifest = JSON.parse(v1Manifest.properties.manifest as string);
      } else {
        manifestEntry.manifest = v1Manifest.properties.manifest as IClientSideComponentManifest;
      }
    }

    const existingEntry: IManifestStoreEntry = this._manifests.get(manifest.id);
    if (existingEntry) {
      if (overwriteExisting || !existingEntry.manifest) {
        existingEntry.manifest = manifestEntry.manifest;
      }
    } else {
      this._manifests.set(manifest.id, manifestEntry);
    }
  }
}



