import SPModuleLoader from './SPModuleLoader';

/* tslint:disable:no-any */
let result: SPModuleLoader = (window as any).spModuleLoader;
/* tslint:enable:no-any */

if (!result) {
  require('./FrameworkPolyfills');

  result = require('./SPModuleLoader').default.instance;
}

/* tslint:disable:export-name */
export = result;
/* tslint:enable:export-name */



