require('set-webpack-public-path!');

import * as Combokeys from 'combokeys';
import * as URL from 'url';

import {
  IClientSideComponentManifest,

  IBundleReference,
  IStandardLibraryModule,
  standardLibraryModules
} from '@microsoft/sp-module-interfaces';

import DeveloperTools from './DeveloperTools/DeveloperTools';
import SystemJsWrapper from './SystemJsWrapper';
import ManifestStore from './ManifestStore';
import DebugManifestProvider from './DebugManifestProvider';

export interface ISPModuleLoader {
  combokeys: typeof Combokeys;

  /* tslint:disable:no-any */
  loadScript(scriptSource: string, globalExportsName?: string): Promise<any>;
  /* tslint:enable:no-any */

  /* tslint:disable:no-any */
  loadStandardLibraryModule(standardLibraryModule: IStandardLibraryModule): Promise<any>;
  /* tslint:enable:no-any */

  /* tslint:disable:no-any */
  loadManifestModules(idOrManifest: string | IClientSideComponentManifest): Promise<any>;
  /* tslint:enable:no-any */

  importManifests(manifests: IClientSideComponentManifest[], overwriteExisting: boolean): void;

  forceLoadDebugManifests(): Promise<void>;

  getManifests(): IClientSideComponentManifest[];

  loadCss(url: string): void;
}

export interface IPartialSpPageContextInfo {
  currentUICultureName: string;
}

export interface IApplicationPreloadedData {
  clientSideApplicationId: string;
  clientSideApplicationManifest: IClientSideComponentManifest;
  manifests: IClientSideComponentManifest[];
  spPageContextInfo: IPartialSpPageContextInfo;
}

interface IDeveloperToolsWrapper {
  default: typeof DeveloperTools;
}

let _allowDebug: boolean = undefined;
export function ensureDebugAllowed(ask: boolean = true): boolean {
  if (!ask || _allowDebug !== undefined) {
    return !!_allowDebug;
  } else {
    // Inform the user that they might be loading debug manifests
    _allowDebug = confirm('WARNING: This page is requesting to load unsafe scripts, which could potentially harm ' +
                          'your computer! Do not proceed unless you trust the developer and understand the risks.\n\n' +
                          'If you are unsure, click "Cancel."');
    return _allowDebug;
  }
}

/**
 * The global module loader for SP client-side applications and webparts.
 *
 * This is an API that lives in a global variable on the window, similar to how systemJS works.
 *
 * @public
 */
export default class SPModuleLoader implements ISPModuleLoader {
  private static _instance: SPModuleLoader;
  private static _headElement: HTMLHeadElement;

  public static get instance(): SPModuleLoader {
    if (!this._instance) {
      this._instance = new SPModuleLoader();
    }

    return this._instance;
  }

  /**
   * @public
   *
   * Combokeys reference. Placed here so any other component can use it without having to bundle it again.
   */
  public combokeys: typeof Combokeys = Combokeys;

  private _developerTools: DeveloperTools;

  constructor() {
    // Watching for hotkeys on the whole document.
    const fullWindowCombokeys: Combokeys.Combokeys = new Combokeys(document.documentElement);

    // Bind the debugger to "ctrl+shift+`"
    fullWindowCombokeys.bind('ctrl+shift+`', (): void => this._toggleDeveloperTools());

    // Easter egg - Konami code. Obfuscate, so it's not obvious from looking at the minimized source.
    const keyMap: { [id: number]: string } = require('combokeys/helpers/special-keys-map');
    fullWindowCombokeys.bind([keyMap[38], keyMap[38], keyMap[40], keyMap[40], keyMap[37],
                    keyMap[39], keyMap[37], keyMap[39], 'b', 'a', keyMap[13]].join(' '), (): void => {
      const audio: HTMLAudioElement =
        new Audio(require('file?name=[sha512:hash:base32:7]_[hash:16].[ext]!./Windows_95_Startup.mp3'));
      audio.volume = 0.25;
      audio.play();
    });

    if (!SPModuleLoader._headElement) {
      SPModuleLoader._headElement = document.getElementsByTagName('head')[0];
    }
  }

  public initialize(preloadedData: IApplicationPreloadedData): Promise<void> {
    return new Promise<void>((resolve: () => void) => {
        ManifestStore.instance.populateManifests(preloadedData);
        this._loadDebugManifests().then(() => {
            if (preloadedData && preloadedData.spPageContextInfo) {
              SystemJsWrapper.instance.currentLocale = preloadedData.spPageContextInfo.currentUICultureName;
            }

            resolve();
          });
    });
  }

  /**
   * Get the component manifest by id.
   *
   * @param id - GUID id of the component.
   */
  /* tslint:disable:no-any */
  public getManifestById(id: string): any {
  /* tslint:enable:no-any */
    return ManifestStore.instance.getManifestById(id);
  }

  /**
   * This is called by the page scripts to start loading the framework. Do not call it from your own code.
   */
  public start(preloadedData: IApplicationPreloadedData): void {
    this.initialize(preloadedData).then(() => {
      if (preloadedData) {
        const applicationManifestId: string = preloadedData.clientSideApplicationId
          ? preloadedData.clientSideApplicationId
          : (preloadedData.clientSideApplicationManifest
            ? preloadedData.clientSideApplicationManifest.id
            : undefined);

        const manifest: IClientSideComponentManifest =
          ManifestStore.instance.getManifestById(applicationManifestId);
        if (manifest) {
          const platformModule: IStandardLibraryModule = standardLibraryModules['@microsoft/sp-client-preview'];

          let loadingComplete: boolean = false;

          Promise.all([
            this.loadStandardLibraryModule(platformModule), // Load the platform in addition to the application
            SystemJsWrapper.instance.loadManifestModules(manifest.id, manifest.loaderConfig)
          ])
            /* tslint:disable:no-any */
            .then((modules: any[]) => {
            /* tslint:enable:no-any */
              loadingComplete = true;

              /* tslint:disable:no-any */
              const platform: any = modules[0];
              /* tslint:enable:no-any */
              const application: {} = modules[1];

              if (!platform || !application) {
                throw new Error(`@microsoft/sp-client-preview or ${manifest.loaderConfig.entryModuleId} failed ` +
                                'to load.');
              } else {
                try {
                  platform._spShell.start(application, preloadedData);
                } catch (e) {
                  console.error(`Error loading application. Error: ${e}`);
                  throw e;
                }
              }

              // @todo VSO #210443: preload WebPart manifests
            }).catch((e) => {
              if (!loadingComplete) {
                console.error(`${manifest.loaderConfig.entryModuleId} failed to load. Error: ${e}`);
              }

              throw e;
            });
        } else {
          throw new Error('Invalid manifest.');
        }
      } else {
        throw new Error('Invalid preloaded data.');
      }
    });
  }

  /**
   * Given a URL, load a script.
   *
   * @param {scriptSource} The script URL.
   * @param {globalExportsName} (optional) If the script isn't an AMD mdoule and loads a global member on the page,
   *                  specify the global member's name.
   * @returns A promse containing the loaded module.
   */
  /* tslint:disable:no-any */
  public loadScript(scriptSource: string, globalExportsName?: string): Promise<any> {
  /* tslint:enable:no-any */
    return SystemJsWrapper.instance.loadScript(scriptSource, globalExportsName);
  }

  /**
   * Load a standard library manifest.
   *
   * @param {standardLibraryModule} The standard library module object.
   * @returns A promse containing the loaded module.
   */
  /* tslint:disable:no-any */
  public loadStandardLibraryModule(standardLibraryModule: IStandardLibraryModule, moduleName?: string): Promise<any> {
  /* tslint:enable:no-any */
    if (NPM_BUILD) {
      // Special case for NPM modules to resolve React, React-DOM, and Flux

      /* tslint:disable:no-any */
      const resultPromise: Promise<any> = this._npmLoadPackage(moduleName);
      /* tslint:enable:no-any */
      if (resultPromise) {
        return resultPromise;
      }
    }

    const references: IBundleReference[] =
      [standardLibraryModule.bundleReference].concat(standardLibraryModule.aliases || []);

    return this._internalLoadManifestModules(references);
  }

  /**
   * Load a manifest, or resolve a manifest ID and load it.
   *
   * @param {idOrManifest} The ID of a manifest, the manifest payload, or an array of IDs. In the case of an array
   *                        of IDs, all of the IDs are considered equally valid, so pick the first one that's resolvable
   *                        to a manifest.
   * @returns A promse containing the loaded module.
   */
  /* tslint:disable:no-any */
  public loadManifestModules(idOrManifest: string | IClientSideComponentManifest): Promise<any> {
  /* tslint:enable:no-any */
    if (typeof idOrManifest === 'string') {
      return this._internalLoadManifestModules([{
        id: idOrManifest as string
      }]);
    } else {
      const manifest: IClientSideComponentManifest = idOrManifest as IClientSideComponentManifest;
      return this._internalLoadManifestModules(manifest);
    }
  }

  /**
   * Loads additional manifests into the manifest store, updating existing manifests.
   *
   * @param {manifests} The manifests to load into the store.
   */
  public importManifests(manifests: IClientSideComponentManifest[], overwriteExisting: boolean = false): void {
    ManifestStore.instance.importManifests(manifests, overwriteExisting);
  }

  /**
   * If debug manfiests haven't already been loaded, force them to load without prompting the user.
   *
   * @returns A promise that is fulfilled when the manifests are loaded.
   */
  public forceLoadDebugManifests(): Promise<void> {
    return this._loadDebugManifests(true, true, 'http://localhost:4321/temp/manifests.js');
  }

  /**
   * Get all registered manifests.
   *
   * @returns All registered manifests.
   */
  public getManifests(): IClientSideComponentManifest[] {
    return ManifestStore.instance.getAllManifests();
  }

  /**
   * Inserts a <link ... /> tag for a stylesheet.
   *
   * @param {url} - The CSS file URL.
   */
  public loadCss(url: string): void {
    const linkTag: HTMLLinkElement = document.createElement('link');
    linkTag.rel = 'stylesheet';
    linkTag.type = 'text/css';
    linkTag.href = url;

    SPModuleLoader._headElement.appendChild(linkTag);
  }

  private _loadDebugManifests(noPrompt: boolean = false,
                              noCache: boolean = false,
                              alternativeManifestsFile?: string): Promise<void> {
    if (noPrompt) {
      _allowDebug = true;
    }

    return DebugManifestProvider.instance.getDebugManifests(noCache, alternativeManifestsFile).then(
      (debugManifests: { [id: string]: IClientSideComponentManifest }) => {
        ManifestStore.instance.populateDebugManifests(debugManifests);
        ensureDebugAllowed(debugManifests && Object.keys(debugManifests).length > 0);
      });
  }

  private _internalLoadManifestModules(referenceOrManifest: IBundleReference[] | IClientSideComponentManifest):
  /* tslint:disable:no-any */
      Promise<any> {
  /* tslint:enable:no-any */
    const references: IBundleReference[] = referenceOrManifest as IBundleReference[];
    let manifest: IClientSideComponentManifest;
    let entryName: string;
    if (references.length) {
      // We have an array of manifests, so find the first one that matches
      for (const reference of references) {
        manifest = ManifestStore.instance.getManifestById(reference.id);
        if (manifest) {
          entryName = reference.entryName;
          break;
        }
      }
    } else {
      manifest = referenceOrManifest as IClientSideComponentManifest;
    }

    if (manifest) {
      return SystemJsWrapper.instance.loadManifestModules(manifest.id, manifest.loaderConfig, entryName);
    } else {
      throw new Error(`Manifest "${JSON.stringify(referenceOrManifest)}" not found.`);
    }
  }

  private _toggleDeveloperTools(): void {
    if (this._developerTools) {
      this._developerTools.toggleDeveloperTools();
    } else {
      const loadDeveloperTools: () => void = (): void => {
        require.ensure(['./DeveloperTools/DeveloperTools'], (require: ((path: string) => IDeveloperToolsWrapper)) => {
          const developerToolsClass: typeof DeveloperTools = require('./DeveloperTools/DeveloperTools').default;
          this._developerTools = developerToolsClass.instance;
          this._developerTools.initialize();

          this._developerTools.showHideDeveloperTools(true);
        }, 'developer-tools');
      };

      // If we're building an NPM module, we need to make sure React, Flux, and React-DOM aren't bundled. Instead,
      // we'll load them from node_modules.
      if (NPM_BUILD) {
        // Ensure the fake react, flux, and react-dom modules are included in this chunk
        require('react');
        require('flux');
        require('react-dom');

        /* tslint:disable:no-any */
        const reactPromise: Promise<any> = this._npmLoadPackage('react');
        const fluxPromise: Promise<any> = this._npmLoadPackage('flux');
        const reactDomPromise: Promise<any> = this._npmLoadPackage('react-dom');
        /* tslint:enable:no-any */

        Promise.all([reactPromise, reactDomPromise, fluxPromise])
               .then((results: any[]) => { // tslint:disable-line:no-any
                 /* tslint:disable:no-any */
                 const react: any = results[0];
                 const reactDom: any = results[1];
                 const flux: any = results[2];
                 /* tslint:enable:no-any */

                 // Inject react, react-dom, and flux into the module cache
                 require.cache[require.resolveWeak('react')].exports = react;
                 require.cache[require.resolveWeak('react-dom')].exports = reactDom;
                 require.cache[require.resolveWeak('flux')].exports = flux;

                 loadDeveloperTools();
               });
      } else {
        loadDeveloperTools();
      }
    }
  }

  /**
   * This function contains some magic to load special packages (React, React-DOM, and Flux) while we're serving
   *  from a local workbench without bunding any of them.
   */
  /* tslint:disable:no-any */
  private _npmLoadPackage(packageName: string): Promise<any> {
  /* tslint:enable:no-any */
    if (NPM_BUILD) {
      const url: typeof URL = require('url');
      const relativeReactUrl: string = '__RELATIVE_REACT_PATH__';
      const relativeFluxUrl: string = '__RELATIVE_FLUX_PATH__';

      if (relativeReactUrl === '__RELATIVE_' + 'REACT_PATH__' ||
          relativeFluxUrl  === '__RELATIVE_' + 'FLUX_PATH__') {
        // If the package hasn't been installed (i.e. - it's NPMX linked), don't try to load from incorrect paths
        return undefined;
      }

      const reactUrl: string = url.resolve(__webpack_public_path__, relativeReactUrl);
      const fluxUrl: string = url.resolve(__webpack_public_path__, relativeFluxUrl);

      // Stick these on the SPModuleLoader object
      /* tslint:disable:no-any */
      const staticContainer: {
        _reactPromise: Promise<any>,
        _fluxPromise: Promise<any>
      } = SPModuleLoader as any;
      /* tslint:enable:no-any */

      if (!staticContainer._reactPromise) {
       staticContainer._reactPromise = this.loadScript(reactUrl);
      }

      if (!staticContainer._fluxPromise) {
        staticContainer._fluxPromise = this.loadScript(fluxUrl);
      }

      switch (packageName) {
        case 'react':
          return staticContainer._reactPromise;

        case 'react-dom':
          /* tslint:disable:no-any */
          return staticContainer._reactPromise.then((react: any) => react.__SECRET_DOM_DO_NOT_USE_OR_YOU_WILL_BE_FIRED);
          /* tslint:enable:no-any */

        case 'flux':
          return staticContainer._fluxPromise;

        default:
          break;
      }
    }

    return undefined;
  }
}



